/* typography */
body {
  font-size: 15px;
  color: #767792;
}

a {
  text-decoration: none;
}

.directorist-content-active div {
  font-size: 15px;
}
.directorist-content-active article {
  margin-bottom: 0;
  border: 0;
  padding-bottom: 0;
}
.directorist-content-active ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.directorist-content-active ul li {
  list-style: none;
  margin: 0;
}
.directorist-content-active a:focus,
.directorist-content-active button:focus {
  outline: none;
}
.directorist-content-active .entry-content h1,
.directorist-content-active .entry-content h2,
.directorist-content-active .entry-content h3,
.directorist-content-active .entry-content h4,
.directorist-content-active .entry-content h5,
.directorist-content-active .entry-content h6 {
  margin-bottom: 10px;
}

@keyframes rotate360 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.reset-pseudo-link:visited, .reset-pseudo-link:active, .reset-pseudo-link:focus {
  outline: none;
  box-shadow: none;
}

